.navbar {
	max-width: 1170px;
	display: flex;
	justify-content: space-between;
	margin-left: auto;
	margin-right: auto;
	align-items: center;
	height: 80px;
	position: sticky;
	position: -webkit-sticky;
}

.logo img {
	height: 60px;
	width: auto;
}
.navbar_links {
	display: flex;
	align-items: center;
	font-family: "Mulish";
	font-weight: 400;
	color: #2654b4;
}
.navbar_links span {
	padding: 0px 10px;
	cursor: pointer;
}
.navbar_basket {
	display: flex;
	align-items: center;
	margin-left: 10px;
}
.navbar_basket_b {
	display: flex;
	justify-content: center;
	cursor: pointer;
	background-color: #bdd9fa;
	padding: 10px 15px;
	border-radius: 5px 0px 0px 5px;
}
.navbar_basket_b img {
	height: 20px;
	padding-right: 5px;
}
.navbar_basket_n {
	cursor: pointer;
	background-color: #bdd9fa;
	margin-left: 3px;
	padding: 10px 15px;
	border-radius: 0px 5px 5px 0px;
}
@media only screen and (max-width: 450px) {
	.navbar {
		transform: scale(0.9);
		flex-direction: column;
		height: max-content;
	}
}
@media (min-width: 600px) and (max-width: 1100px) {
	.navbar {
		padding: 0px 30px;
	}
}
