.firstfold_con {
	height: 500px;
}
.firstfold {
	background: #fdedd3;
}
.firstfold_container {
	margin-left: auto;
	margin-right: auto;
	max-width: 1170px;
	height: max-content;
	padding: 0px 30px;
	position: relative;
}
.firstfold_text {
	width: 56%;
	display: flex;
	flex-direction: column;
	padding-top: 80px;
	padding-bottom: 80px;
}
.firstfold_text_title {
	font-family: "Lora", sans-serif;
	font-weight: 300;
	margin-bottom: -10px;
}
.firstfold_text_p {
	font-family: "Mulish", sans-serif;
}

.firstfold_card {
	font-size: 1rem;
	padding: 10px 30px;
	width: 270px;
	border-radius: 10px;
	background: white;
	border: 2px solid #e1e1e1;
	position: absolute;
	right: 50px;
	top: 80px;
	font-family: "Mulish";
}
.firstfold_card .bold {
	font-weight: bold;
}
.firstfold_card p::before {
	content: "✔";
	padding-right: 10px;
	color: #19a14c;
}
.card_price {
	display: flex;
	justify-content: space-between;
}
.card_price .price {
	color: #19a14c;
	font-weight: bold;
}
.card_add {
	background: #bdd9fa;
	margin-right: 2px;
	padding: 5px 10px;
	color: #2654b4;
	font-weight: bold;
}
.card_box span:nth-child(1) {
	border-radius: 5px 0px 0px 5px;
	cursor: pointer;
}
.card_box span:nth-child(3) {
	border-radius: 0px 5px 5px 0px;
	cursor: pointer;
}
.add_to {
	display: flex;
	justify-content: center;
	width: 100%;
	margin-top: 24px;
	margin-bottom: 20px;
	padding: 10px 30px;
	font-size: 1rem;
	cursor: pointer;
	background: #2654b4;
	border: none;
	border-radius: 5px;
	color: white;
}
.add_to img {
	height: 15px;
	padding-right: 5px;
}
@media screen and (max-width: 600px) {
	.firstfold_container {
		display: flex;
		flex-direction: column;
	}
	.firstfold_text {
		width: 100%;
	}
	.firstfold_card {
		position: absolute;
		top: 200px;
		left: auto;
		right: auto;
		transform: scale(0.6);
	}
}
