.third_fold {
	background: #efece6;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.third_fold_container {
	max-width: 1170px;
}
.third_fold_container h1 {
	padding-top: 80px;
	padding-bottom: 40px;
	text-align: center;
	font-size: 36px;
	font-family: "Lora";
	font-weight: lighter;
}
.third_fold_container::after {
	content: "";
	display: table;
	clear: both;
}
.column {
	float: left;
	width: 33.33%;
}
.card {
	font-family: "Lora";
	height: 15vh;
	margin-bottom: 30px;
	margin-right: 20px;
	border-radius: 5px;
	background: white;
	padding: 20px;
	position: relative;
	border: 2px solid #e1e1e1;
}
.card h5 {
	color: black;
	opacity: 0.5;
}
@media screen and (max-width: 650px) {
	.column {
		width: 100%;
	}
}
@media (min-width: 650px) and (max-width: 1040px) {
	.column {
		width: 50%;
	}
}
@media screen and (max-width: 1170px) {
	.third_fold {
		padding-left: 30px;
		padding-right: 10px;
	}
}
.card_footer {
	width: 90%;
	display: flex;
	justify-content: space-between;
	position: absolute;
	bottom: 0%;
	font-family: "Mulish";
}
.card_footer h3:nth-child(1) {
	color: #19a14c;
}
.footer_right {
	color: #2654b4;
	flex: 0.7;
	display: flex;
	align-items: center;
	justify-content: space-around;
}
.footer_right img {
	height: 20px;
}
.footer_right h3 {
	text-decoration: underline;
}
.footer_rightAdd {
	padding-right: 15px;
}
.third_fold button {
	margin-top: 10px;
	width: 200px;
	color: white;
	background: #2654b4;
	margin-bottom: 40px;
	padding: 10px;
	border-radius: 5px;
	border: none;
}
