.second_fold {
	max-width: 1170px;
	margin-top: 80px;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	justify-content: space-between;
	margin-bottom: 112px;
}
.second_fold_text {
	font-family: "Mulish";
	width: 40%;
}
.text_title {
	font-size: 16px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.text_title span {
	cursor: pointer;
}
.second_fold_img {
	padding-left: 30px;
}
.second_fold_img img {
	width: 570px;
	height: 370px;
	animation-timing-function: ease-in-out;
	animation-name: leftFadeOut;
	animation-duration: 1s;
}
@keyframes leftFadeOut {
	0% {
		margin-right: -100px;
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.show {
	height: max-content;
	animation-timing-function: ease-in-out;
	animation-name: topFadeOut;
	animation-duration: 1s;
	color: black;
	margin-top: -15px;
}
@keyframes topFadeOut {
	0% {
		margin-top: -40px;
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media only screen and (max-width: 650px) {
	.second_fold {
		display: flex;
		flex-direction: column-reverse;
	}
	.second_fold_text {
		width: 100%;
		z-index: 99;
	}
	.second_fold_img img {
		width: 270px;
		height: 175px;
		animation-timing-function: ease-in-out;
		animation-name: leftFadeOut;
		animation-duration: 3s;
	}
}
@media only screen and (max-width: 1170px) {
	.second_fold {
		padding: 0px 30px;
	}
}
@media (min-width: 650px) and (max-width: 1050px) {
	.text {
		font-size: 12px;
	}
	.text_title {
		font-size: 15px;
	}
	.second_fold_img img {
		width: 500px;
	}
}
