@import url(https://fonts.googleapis.com/css2?&family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400&display=swap);
body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	box-sizing: border-box;
}

.firstfold_con {
	height: 500px;
}
.firstfold {
	background: #fdedd3;
}
.firstfold_container {
	margin-left: auto;
	margin-right: auto;
	max-width: 1170px;
	height: -webkit-max-content;
	height: max-content;
	padding: 0px 30px;
	position: relative;
}
.firstfold_text {
	width: 56%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	padding-top: 80px;
	padding-bottom: 80px;
}
.firstfold_text_title {
	font-family: "Lora", sans-serif;
	font-weight: 300;
	margin-bottom: -10px;
}
.firstfold_text_p {
	font-family: "Mulish", sans-serif;
}

.firstfold_card {
	font-size: 1rem;
	padding: 10px 30px;
	width: 270px;
	border-radius: 10px;
	background: white;
	border: 2px solid #e1e1e1;
	position: absolute;
	right: 50px;
	top: 80px;
	font-family: "Mulish";
}
.firstfold_card .bold {
	font-weight: bold;
}
.firstfold_card p::before {
	content: "✔";
	padding-right: 10px;
	color: #19a14c;
}
.card_price {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.card_price .price {
	color: #19a14c;
	font-weight: bold;
}
.card_add {
	background: #bdd9fa;
	margin-right: 2px;
	padding: 5px 10px;
	color: #2654b4;
	font-weight: bold;
}
.card_box span:nth-child(1) {
	border-radius: 5px 0px 0px 5px;
	cursor: pointer;
}
.card_box span:nth-child(3) {
	border-radius: 0px 5px 5px 0px;
	cursor: pointer;
}
.add_to {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
	margin-top: 24px;
	margin-bottom: 20px;
	padding: 10px 30px;
	font-size: 1rem;
	cursor: pointer;
	background: #2654b4;
	border: none;
	border-radius: 5px;
	color: white;
}
.add_to img {
	height: 15px;
	padding-right: 5px;
}
@media screen and (max-width: 600px) {
	.firstfold_container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}
	.firstfold_text {
		width: 100%;
	}
	.firstfold_card {
		position: absolute;
		top: 200px;
		left: auto;
		right: auto;
		-webkit-transform: scale(0.6);
		        transform: scale(0.6);
	}
}

.navbar {
	max-width: 1170px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-left: auto;
	margin-right: auto;
	-webkit-align-items: center;
	        align-items: center;
	height: 80px;
	position: sticky;
	position: -webkit-sticky;
}

.logo img {
	height: 60px;
	width: auto;
}
.navbar_links {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	font-family: "Mulish";
	font-weight: 400;
	color: #2654b4;
}
.navbar_links span {
	padding: 0px 10px;
	cursor: pointer;
}
.navbar_basket {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 10px;
}
.navbar_basket_b {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
	background-color: #bdd9fa;
	padding: 10px 15px;
	border-radius: 5px 0px 0px 5px;
}
.navbar_basket_b img {
	height: 20px;
	padding-right: 5px;
}
.navbar_basket_n {
	cursor: pointer;
	background-color: #bdd9fa;
	margin-left: 3px;
	padding: 10px 15px;
	border-radius: 0px 5px 5px 0px;
}
@media only screen and (max-width: 450px) {
	.navbar {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
		-webkit-flex-direction: column;
		        flex-direction: column;
		height: -webkit-max-content;
		height: max-content;
	}
}
@media (min-width: 600px) and (max-width: 1100px) {
	.navbar {
		padding: 0px 30px;
	}
}

.second_fold {
	max-width: 1170px;
	margin-top: 80px;
	margin-left: auto;
	margin-right: auto;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-bottom: 112px;
}
.second_fold_text {
	font-family: "Mulish";
	width: 40%;
}
.text_title {
	font-size: 16px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.text_title span {
	cursor: pointer;
}
.second_fold_img {
	padding-left: 30px;
}
.second_fold_img img {
	width: 570px;
	height: 370px;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
	-webkit-animation-name: leftFadeOut;
	        animation-name: leftFadeOut;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
}
@-webkit-keyframes leftFadeOut {
	0% {
		margin-right: -100px;
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes leftFadeOut {
	0% {
		margin-right: -100px;
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.show {
	height: -webkit-max-content;
	height: max-content;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
	-webkit-animation-name: topFadeOut;
	        animation-name: topFadeOut;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	color: black;
	margin-top: -15px;
}
@-webkit-keyframes topFadeOut {
	0% {
		margin-top: -40px;
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes topFadeOut {
	0% {
		margin-top: -40px;
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media only screen and (max-width: 650px) {
	.second_fold {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column-reverse;
		        flex-direction: column-reverse;
	}
	.second_fold_text {
		width: 100%;
		z-index: 99;
	}
	.second_fold_img img {
		width: 270px;
		height: 175px;
		-webkit-animation-timing-function: ease-in-out;
		        animation-timing-function: ease-in-out;
		-webkit-animation-name: leftFadeOut;
		        animation-name: leftFadeOut;
		-webkit-animation-duration: 3s;
		        animation-duration: 3s;
	}
}
@media only screen and (max-width: 1170px) {
	.second_fold {
		padding: 0px 30px;
	}
}
@media (min-width: 650px) and (max-width: 1050px) {
	.text {
		font-size: 12px;
	}
	.text_title {
		font-size: 15px;
	}
	.second_fold_img img {
		width: 500px;
	}
}

.third_fold {
	background: #efece6;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}
.third_fold_container {
	max-width: 1170px;
}
.third_fold_container h1 {
	padding-top: 80px;
	padding-bottom: 40px;
	text-align: center;
	font-size: 36px;
	font-family: "Lora";
	font-weight: lighter;
}
.third_fold_container::after {
	content: "";
	display: table;
	clear: both;
}
.column {
	float: left;
	width: 33.33%;
}
.card {
	font-family: "Lora";
	height: 15vh;
	margin-bottom: 30px;
	margin-right: 20px;
	border-radius: 5px;
	background: white;
	padding: 20px;
	position: relative;
	border: 2px solid #e1e1e1;
}
.card h5 {
	color: black;
	opacity: 0.5;
}
@media screen and (max-width: 650px) {
	.column {
		width: 100%;
	}
}
@media (min-width: 650px) and (max-width: 1040px) {
	.column {
		width: 50%;
	}
}
@media screen and (max-width: 1170px) {
	.third_fold {
		padding-left: 30px;
		padding-right: 10px;
	}
}
.card_footer {
	width: 90%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	position: absolute;
	bottom: 0%;
	font-family: "Mulish";
}
.card_footer h3:nth-child(1) {
	color: #19a14c;
}
.footer_right {
	color: #2654b4;
	-webkit-flex: 0.7 1;
	        flex: 0.7 1;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}
.footer_right img {
	height: 20px;
}
.footer_right h3 {
	text-decoration: underline;
}
.footer_rightAdd {
	padding-right: 15px;
}
.third_fold button {
	margin-top: 10px;
	width: 200px;
	color: white;
	background: #2654b4;
	margin-bottom: 40px;
	padding: 10px;
	border-radius: 5px;
	border: none;
}

